<template>
    <div class="regist">
        <div class="regist-wrap">
            <div class="regist-wrap__title">
                <img src="../assets/images/logo.png" alt="中山大学logo">
                <p>中山大学课程项目组队信息管理网站</p>
            </div>
            <div class="regist-wrap__box">
                <el-form :model="student" :rules="rules" ref="student" label-width="0px"
                         class="regist-wrap__box__content">
                    <div v-if="errorInfo" class="regist-wrap__box__content__err">
                        <span>{{errInfo}}</span>
                    </div>
                    <el-form-item prop="name">
                        <el-input v-model="student.name" placeholder="账号/学工号"></el-input>
                    </el-form-item>

                    <el-form-item prop="password">
                        <el-input type="password" placeholder="密码" v-model="student.password"
                                  @keyup.enter.native="submitForm('student')"></el-input>
                    </el-form-item>

                    <el-form-item prop="checkPass">
                        <el-input type="password" placeholder="确认密码" v-model="student.checkPass"
                                  @keyup.enter.native="submitForm('student')"></el-input>
                    </el-form-item>

                    <el-form-item prop="validate">
                        <el-input v-model="student.validate" class="validate-code" placeholder="验证码"></el-input>
                        <div class="code" @click="refreshCode">
                            <s-identify :identifyCode="identifyCode"></s-identify>
                        </div>
                    </el-form-item>
                    <div class="regist-wrap__box__content__btn">
                        <el-button type="primary" @click="submitForm('student')">注册</el-button>
                    </div>
                </el-form>
            </div>
        </div>
    </div>
</template>

<script>
    import Sidentify from './identify/Identify'

    export default {
        name: 'regist',
        data () {
            return {
                identifyCodes: '1234567890',
                identifyCode: '',
                errorInfo: false,
                student: {
                    name: '',
                    password: '',
                    checkPass: '',
                    validate: ''
                },
                rules: {
                    name: [
                        {required: true, message: '请输入用户名', trigger: 'change'}
                    ],
                    password: [
                        {required: true, message: '请输入密码', trigger: 'change'}
                    ],
                    checkPass: [
                        {required: true, message: '请再次输入密码', trigger: 'change'}
                    ],
                    validate: [
                        {required: true, message: '请输入验证码', trigger: 'change'}
                    ]
                }
            }
        },
        components: {
            's-identify': Sidentify
        },
        mounted () {
            this.identifyCode = ''
            this.makeCode(this.identifyCodes, 4)
        },
        methods: {
            submitForm (formName) {
                this.$router.replace('/home') // 需验证再跳转，此处仅为演示效果
                const self = this
                self.$refs[formName].validate((valid) => {
                    if (valid) {
                        localStorage.setItem('ms_username', self.student.name)
                        localStorage.setItem('ms_user', JSON.stringify(self.student))
                        console.log(JSON.stringify(self.student))
                        self.$http.post('/api/user/regist', JSON.stringify(self.student))
                            .then((response) => {
                                console.log(response)
                                if (response.data === -1) {
                                    self.errorInfo = true
                                    self.errInfo = '该用户不存在'
                                    console.log('该用户不存在')
                                } else if (response.data === 0) {
                                    console.log('密码错误')
                                    self.errorInfo = true
                                    self.errInfo = '密码错误'
                                } else if (response.status === 200) {
                                    self.$router.push('/readme')
                                }
                            }).then((error) => {
                            console.log(error)
                        })
                    } else {
                        console.log('error submit!!')
                        return false
                    }
                })
            },
            handleCommand () {
                this.$router.push('/register')
            },
            randomNum (min, max) {
                return Math.floor(Math.random() * (max - min) + min)
            },
            refreshCode () {
                this.identifyCode = ''
                this.makeCode(this.identifyCodes, 4)
            },
            makeCode (o, l) {
                for (let i = 0; i < l; i++) {
                    this.identifyCode += this.identifyCodes[this.randomNum(0, this.identifyCodes.length)]
                }
                console.log(this.identifyCode)
            }
        }
    }
</script>

<style lang="scss">
    .regist {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: url("../assets/images/bg.png") no-repeat;
        background-size: cover;

        .regist-wrap {
            opacity: 1;
            border:2px solid #f0f0f0;
            width: 500px;
            margin: 100px auto 0 auto;
            background-color: #e5e3e4;

            .el-form-item{
                margin-bottom:5px;
            }

            &__title {
                height:80px;
                display: flex;
                align-items: center;
                padding:0 60px;
                background-color: #CEECF5;
                margin-bottom:10px;

                img {
                    width: 50px;
                    display: inline-block;
                    border-radius: 50%;
                }
                p{
                    display: inline-block;
                    font-size:20px;
                    margin-left:5px;
                    font-weight:bold;
                }
            }

            &__box {
                margin:0 10%;
                text-align: center;

                &__content {
                    &__err {
                        span {
                            color: red;
                        }
                    }

                    &__btn {
                        text-align: center;
                        margin:10px 0 20px 0;

                        .el-button {
                            width: 100%;
                            height: 36px;
                        }
                    }

                }
            }
        }

        .code {
            width: 110px;
            height: 37px;
            border: 2px solid #ccc;
            float: right;
            margin-right: 5px;
        }

        .validate-code {
            width: 180px;
            float: left;
        }
    }
</style>



