<template>
    <div class="login">
        <div class="login-wrap">
            <div class="login-wrap__title">
                <img src="../assets/images/logo.png" alt="中山大学logo">
                <p>中山大学课程项目组队信息管理网站</p>
            </div>
            <div class="login-wrap__box">
                <el-form :model="student" :rules="rules" ref="student" label-width="0px"
                         class="login-wrap__box__content">
                    <div v-if="errorInfo" class="login-wrap__box__content__err">
                        <span>{{errInfo}}</span>
                    </div>
                    <el-form-item prop="name">
                        <el-input v-model="student.name" placeholder="账号/学工号"></el-input>
                    </el-form-item>
                    <el-form-item prop="password">
                        <el-input type="password" placeholder="密码" v-model="student.password"
                                  @keyup.enter.native="submitForm('student')"></el-input>
                    </el-form-item>
                    <div class="login-wrap__box__content__btn">
                        <el-button type="primary" @click="submitForm('student')">登录</el-button>
                    </div>
                    <p class="login-wrap__box__content__register"
                       @click="handleCommand()">还没有注册？点击注册</p>
                </el-form>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'login',
        data () {
            return {
                errorInfo: false,
                student: {
                    name: '',
                    password: ''
                },
                rules: {
                    name: [
                        {required: true, message: '请输入用户名', trigger: 'change'}
                    ],
                    password: [
                        {required: true, message: '请输入密码', trigger: 'change'}
                    ]
                }
            }
        },
        mounted () {
            this.identifyCode = ''
            this.makeCode(this.identifyCodes, 4)
        },
        methods: {
            submitForm (formName) {
                this.$router.replace('/home') // 需验证再跳转，此处仅为演示效果
                const self = this
                self.$refs[formName].validate((valid) => {
                    if (valid) {
                        localStorage.setItem('ms_username', self.student.name)
                        localStorage.setItem('ms_user', JSON.stringify(self.student))
                        console.log(JSON.stringify(self.student))
                        self.$http.post('/api/user/login', JSON.stringify(self.student))
                            .then((response) => {
                                console.log(response)
                                if (response.data === -1) {
                                    self.errorInfo = true
                                    self.errInfo = '该用户不存在'
                                    console.log('该用户不存在')
                                } else if (response.data === 0) {
                                    console.log('密码错误')
                                    self.errorInfo = true
                                    self.errInfo = '密码错误'
                                } else if (response.status === 200) {
                                    self.$router.push('/readme')
                                }
                            }).then((error) => {
                            console.log(error)
                        })
                    } else {
                        console.log('error submit!!')
                        return false
                    }
                })
            },
            handleCommand () {
                this.$router.replace('/regist')
            }
        }
    }
</script>

<style lang="scss">
    .login {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: url("../assets/images/bg.png") no-repeat;
        background-size: cover;

        .login-wrap {
            opacity: 1;
            border:2px solid #f0f0f0;
            width: 500px;
            margin: 100px auto 0 auto;
            background-color: #F5F6CE;

            .el-form-item{
                margin-bottom:5px;
            }

            &__title {
                height:100px;
                display: flex;
                align-items: center;
                padding:0 40px;
                background-color: #CEECF5;
                margin-bottom:10px;

                img {
                    width: 60px;
                    display: inline-block;
                    border-radius: 50%;
                }
                p{
                    display: inline-block;
                    font-size:20px;
                    margin-left:5px;
                    font-weight:bold;
                }
            }

            &__box {
                margin:20px 10%;
                text-align: center;

                &__content {
                    &__err {
                        span {
                            color: red;
                        }
                    }

                    &__btn {
                        text-align: center;

                        .el-button {
                            width: 100%;
                            height: 36px;
                        }
                    }

                    &__register {
                        font-size: 16px;
                        line-height: 20px;
                        color: #f97112;
                        cursor: pointer;
                        text-decoration: underline;
                    }
                }
            }
        }

        .code {
            width: 110px;
            height: 37px;
            border: 2px solid #ccc;
            float: right;
            margin-right: 5px;
        }

        .validate-code {
            width: 180px;
            float: left;
        }
    }
</style>



